/* landingPage.css */

:root {
    --primary-color:#1d7933ff;
    --secondary-green:rgb(22, 87, 37);
    --light-gray: #f5f5f5;
    --dark-gray: #333;
    --font-stack: 'Helvetica Neue', Arial, sans-serif;
  }
  
  body, html {
    margin: 0;
    padding: 0;
    font-family: var(--font-stack);
    color: var(--dark-gray);
  }
  
  .landingpage-header {
    background-color: var(--primary-color);
    color: white;
    padding: 1rem 2rem;
    display:flex;
    align-items: baseline;
  }

  .landingpage-header img {
    cursor:pointer;
  }
  
  .landingpage-header h1 {
    margin: 0;
  }
  
  .landingpage-hero {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 2rem;
    height: 450px;
    background: var(--primary-color);
  }

  .landingpage-hero-text {
    max-width: 50%;
    text-align:left;
  }
  
  .landingpage-hero-text h2 {
    margin-top: 0;
    color: white;
    font-size: 60px;
  }
  
  .landingpage-hero-image {
    background-color: var(--secondary-green);
    /* background-color: white; */
    border-radius: 10px;
    padding:35px 30px 35px 30px;
    margin-top:50px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
    margin-right: 7%;
    width: 45%;
    display:flex;
  }

  .landingpage-hero-image-info div {
    padding:10px;
    border-radius: 10px;;
    background-color: var(--light-gray);
    box-shadow: inset 0 4px 20px rgba(0, 0, 0, 0.3);
    margin-top:20px;
    width:100%;
  }
  
  .landingpage-hero-image img {
    height: auto;
    width: 250px;
    /* background-color: var(--primary-color); */
    margin-right:20px;
    /* border-radius: 15px; */
    margin-top:15px;
    /* box-shadow: inset 0 4px 20px rgba(0, 0, 0, 0.3); */
  }

  .landingpage-hero-image-img {
    text-align: center;
  }

  .landingpage-hero-image-img p {
    color:var(--dark-gray);
  }

  .landingpage-hero-image-info p {
    color:var(--light-gray);
  }

  .landingpage-hero-image-info h1 {
    color:var(--secondary-green);
    font-size: 27px;
    font-weight: bolder;
  }
  
  .landingpage-section {
    padding: 3rem 2rem;
  }
  
  .landingpage-section:nth-child(even) {
    background: var(--light-gray);
  }
  
  .landingpage-ratings {
    text-align: center;
    height:150px;
  }
  
  .landingpage-ratings h3 {
    color: var(--primary-color);
  }

  .landingpage-hero-text p {
    color: white;
    margin:10px;
  }
  
  .landingpage-ratings .stars {
    font-size: 2rem;
    color: gold;
  }
  
  .landingpage-usecases {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
  }

  .landingpage-icon {
    font-size:65px;
    padding-right:10px;
  }

  .landingpage-info-icon {
    font-size:40px;
  }
  
  
  .landingpage-usecase {
    background: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 1.5rem;
    text-align: left;

    display: grid;
    grid-template-columns: 0.5fr 1fr;
    align-items: center;
    justify-content: center;
  }

  .landingpage-usecase h4 {
    font-size:20px;
  }

  .landingpage-usecase > :first-child {
    justify-self: center;
    align-self: center;
  }
  
  .landingpage-usecase h4 {
    margin-bottom: 0.5rem;
    color: var(--primary-color);
  }
  
  .landingpage-footer {
    background: var(--dark-gray);
    color: white;
    padding: 2rem;
    text-align: center;
  }
  